import React, { useState } from "react";
import LocationAndCategory from "./tabs/LocationAndCategory";
import TopicContent from "./tabs/TopicContent";
import Media from "./tabs/Media";
import Preview from "./tabs/Preview";
import Header from "../../semantics/header";
import Footer from "../../semantics/footer";
import checkMark from "../../assets/images/check-mark.png";

import "./style.scss";

class TopicSuggestion extends React.Component {
  state = {
    categories: [],
    page: 1,
    locationAndCategory: {
      form: {
        categories: [],
        categoryId: null,
        location: ""
      }
    },
    topicContent: {
      form: {
        title: "",
        description: "",
        amount: ""
      }
    },
    media: {
      form: {
        file: null
      }
    },
    review: {
      form: {
        category: null,
        location: ""
      }
    },
    checkMarkOne: false,
    checkMarkTwo: false,
    checkMarkThree: false,
    checkMarkFour: false,
    submittedOne: false,
    submittedTwo: false,
    submittedThree: false,
    submittedFour: false,
    typed: false
  };

  componentDidMount() {
    this.getCategories();
  }

  firstPageChange = ({ target: { name, value } }) => {
    this.setState(prevState => ({
      ...prevState,
      locationAndCategory: {
        ...prevState.locationAndCategory,
        form: { ...prevState.locationAndCategory.form, [name]: value }
      }
    }));

    // const { locationAndCategory } = this.state;
    // locationAndCategory[e.target.name] = e.target.value;
    // this.setState({ locationAndCategory });
  };

  secondPageChange = ({ target: { name, value } }) => {
    this.setState(prevState => ({
      ...prevState,
      topicContent: {
        ...prevState.topicContent,
        form: { ...prevState.topicContent.form, [name]: value }
      }
    }));

    // if (e.target.value) {
    //   this.setState({
    //     typed: true
    //   });
    // }
  };

  onCategoryChange = value => {
    this.setState(prevState => ({
      ...prevState,
      locationAndCategory: {
        ...prevState.locationAndCategory,
        form: { ...prevState.locationAndCategory.form, category: value }
      }
    }));
    console.log(this.state.topicContent.form.title);
  };

  moveNextPage = page => {
    const {
      submittedOne,
      submittedTwo,
      submittedThree,
      submittedFour
    } = this.state;

    this.setState({
      submitted: true
    });
    const {
      locationAndCategory: {
        form: { category, location }
      },
      topicContent: {
        form: { title, description, amount }
      }
    } = this.state;

    console.log(submitted, location);
    // location and category
    if (submitted && location) {
      this.setState({
        page: 2,
        checkMarkOne: true
      });
    }

    // topic content
    if (submitted && title && description && amount) {
      this.setState({
        page: 3,
        checkMarkOne: true,
        checkMarkTwo: true
      });
    }

    // media
    if (submitted) {
      this.setState({
        page: 4,
        checkMarkOne: true,
        checkMarkTwo: true,
        checkMarkThree: true
      });
    }
  };

  publishHandler = async e => {
    console.log("publishhh");
    let token = localStorage.getItem("authToken");
    try {
      if (token) {
        const res = await fetch("https://2.evollai.com/api/topic", {
          method: "POST",
          body: JSON.stringify({
            authToken: token,
            osType: 3,
            categoryId: "",
            addres: "",
            lat: 1,
            lng: "1",
            title: "",
            description: "",
            file: null
          }),
          headers: {
            "Content-Type": "application/json",
            "Accept-Language": "*",
            language: 1
          }
        });
        const data = await res.json();
        console.log(data);
      } else {
        console.log("no token");
      }
    } catch (error) {
      console.log(error);
    }
  };

  getCategories = async () => {
    const {
      locationAndCategory: {
        form: { categories }
      }
    } = this.state;
    try {
      const response = await fetch("https://2.evollai.com/api/topic/category", {
        method: "GET",
        headers: {
          "Content-Type": "application/json",
          "Accept-Language": "*"
        }
      });
      const result = await response.json();
      console.log(result, "categories");
      if (result) {
        this.setState({
          categories: result.data
        });
      }
    } catch (error) {
      console.log(error);
    }
  };

  sendData = images => {
    this.setState({ imagesToPreview: images });
  };

  render() {
    const {
      form: { location, categories, categoryId }
    } = this.state.locationAndCategory;
    const {
      form: { title, description, amount }
    } = this.state.locationAndCategory;

    const {
      checkMarkOne,
      checkMarkTwo,
      checkMarkThree,
      checkMarkFour,
      page
    } = this.state;
    console.log(this.state.categories, "catttegories");
    return (
      <>
        <Header />
        <div className="suggest-topic">
          <div className="tabs">
            <ul>
              <li>
                Category and <br />
                <span>location</span>
                {checkMarkOne && <img src={checkMark} alt="check-mark" />}
              </li>
              <li>
                Topic content
                {checkMarkOne && checkMarkTwo && (
                  <img src={checkMark} alt="check-mark" />
                )}
              </li>
              <li>
                Media
                {checkMarkOne && checkMarkTwo && checkMarkThree && (
                  <img src={checkMark} alt="check-mark" />
                )}
              </li>
              <li>Preview</li>
            </ul>
          </div>
          <div className="tab-content">
            {/* {page === 1 ? (
              <LocationAndCategory
                moveNextPage={() => this.moveNextPage(1)}
                onChange={this.firstPageChange}
                onCategoryChange={this.onCategoryChange}
                location={location}
                categories={this.state.categories}
                categoryId={categoryId}
                submitted={this.state.submitted}
              />
            ) : page === 2 ? (
              <TopicContent
                onChange={this.secondPageChange}
                moveNextPage={() => this.moveNextPage(2)}
                title={title}
                description={description}
                amount={amount}
                submitted={this.state.submitted}
                typed={this.state.typed}
              />
            ) : page === 3 ? (
              <Media moveNextPage={() => this.moveNextPage(3)} />
            ) : (
              <Preview publish={this.publishHandler} />
            )} */}
            <LocationAndCategory
              moveNextPage={() => this.moveNextPage(1)}
              onChange={this.firstPageChange}
              onCategoryChange={this.onCategoryChange}
              location={location}
              categories={this.state.categories}
              categoryId={categoryId}
              submitted={this.state.submitted}
            />
            <TopicContent
              onChange={this.secondPageChange}
              moveNextPage={() => this.moveNextPage(2)}
              title={title}
              description={description}
              amount={amount}
              submitted={this.state.submitted}
              typed={this.state.typed}
            />
            <Media moveNextPage={() => this.moveNextPage(3)} />
            <Preview publish={this.publishHandler} />
          </div>
        </div>
        <Footer />
      </>
    );
  }
}

export default TopicSuggestion;
